/* ESTILOS NORMALES (para móviles en vertical) */
@media (max-width: 767px) {
  header {
    display: flex;
    color: white;
    width: 100%;
    text-align: center;
    justify-content: center;
  }
  .background-img {
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -1;
  }

  header i {
    color: white;
    padding: 1rem 0.8rem 0;
    font-size: 2rem;
    /* margin-right: 1px; */
  }
  .btnsignin {
    color: white;
    animation: animateright 2s;
    margin-top: 1rem;
    padding: 0.5rem;
    align-self: center;
    background-color: rgb(0, 100, 0);
    /* letter-spacing: 0.2rem; */
    font-size: 0.5rem;
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
  }
  .btnsignup {
    color: white;
    margin-top: 1rem;
    padding: 0.5rem;
    background-color: rgb(0, 100, 0);
    align-self: center;
    /* letter-spacing: 0.2rem; */
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    animation: animateleft 2s ease-in;
    font-size: 0.5rem;
  }
}

