/* Escritorios muy anchos  */

@media (min-width: 1200px) {
  .chevron-all-cities-up {
    transform: rotate(-90deg);
  }

  header {
    display: flex;
    color: white;
    width: 100%;
    text-align: center;
    justify-content: center;
  }
  header i:hover {
    opacity: 1;
    cursor: pointer;
  }
  header i {
    color: white;
    padding: 1rem 0.8rem 0;
    font-size: 2.5rem;
    opacity: 65%;

    /* margin-right: 1px; */
  }

  .btnsignin {
    color: white;
    animation: animateright 2s;
    margin-top: 1rem;
    padding: 0.5rem;
    align-self: center;
    background-color: rgb(0, 100, 0);
    letter-spacing: 0.2rem;
    font-size: 0.8rem;
    cursor: pointer;
    opacity: 65%;
    border-bottom-left-radius: 15px;
    border-top-left-radius: 15px;
  }
  .btnsignup {
    color: white;
    margin-top: 0.7rem;
    padding: 0.5rem;
    background-color: rgb(0, 100, 0);
    align-self: flex-start;
    opacity: 65%;
    letter-spacing: 0.2rem;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    animation: animateleft 2s ease-in;
    font-size: 0.8rem;
    cursor: pointer;
  }
  .btnsignup:hover {
    opacity: 1;
  }
  .btnsignin:hover {
    opacity: 1;
  }
  .background-img {
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -1;
  }
  .user-icon {
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;
  }
  .user-logged {
    color: white;
    padding: 0.5rem;
    opacity: 65%;
    height: 49%;
    cursor: pointer;
    border-radius: 50%;
    z-index: 1;
  }
  .user-logged:hover {
    opacity: 1;
  }
}
@keyframes animateright {
  0% {
    margin-right: -50px;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes animateleft {
  0% {
    margin-left: -50px;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* ESTILOS NORMALES (para móviles en vertical) */

@media (max-width: 767px) {
  main {
    display: flex;
    width: 100%;
    flex-grow: 1;
  }
  main .city {
    display: flex;
    flex-direction: column;
    /* justify-content: center;
    text-align: center; */
    /* height: 100%; */
    margin-left: 0.9rem;
    overflow: auto;
    width: 45%;
    color: rgb(255, 255, 255);
  }

  main .home {
    display: flex;
    flex-direction: column;
    margin-left: 0.8rem;
    /* justify-content: center;
    text-align: center; */
    width: 45%;
    color: rgb(255, 255, 255);
  }
  main .title {
    padding-top: 2rem;
    align-self: center;
    height: 20%;
    letter-spacing: 0.4rem;
    font-size: 1rem;
    font-weight: bold;
  }
  main .slogan {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5rem;
    /* justify-items: center; */
  }
  .subtitle {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5rem;
  }
  p {
    font-weight: bold;
    font-size: 0.8rem;
    /* letter-spacing: 0.2rem; */
  }
  .toLink-Cities {
    text-align: center;
    width: 80px;
    font-size: 0.8rem;
    margin-top: 2.5rem;
    text-decoration: none;
    padding: 0.5rem;
    background-color: rgb(0, 100, 0);
    border: none;
    border-radius: 5px;
    color: rgb(255, 255, 255);
  }
  .action-all-cities {
    display: flex;
    align-self: center;
    height: 60%;
    width: 100%;
    justify-content: center;
  }
  .action-all-cities Button {
    height: 60px;
    width: 60px;
    box-shadow: 0 0 5px 5px rgb(0, 0, 0);
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0);
    color: rgb(0, 0, 0);
    font-weight: bold;
    cursor: pointer;
    align-self: flex-end;
    margin: 1rem 0 0 1rem;
  }
  .action-all-cities Button:hover {
    background-color: #0578a2;
    font-weight: bold;
  }

  main .all-cities:not(".filters") {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    height: 100%;
    overflow: auto;
    width: 100%;

    color: rgb(255, 255, 255);
  }
  .filters {
    display: flex;
    margin: 1rem;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-around;
    width: 100%;
  }
  input {
    display: flex;
    margin: 0.5rem;
    height: 25%;
    text-align: center;
    background-color: transparent;
    color: white;
    padding: 0.3rem;
    max-height: 25%;
  }
  input:-ms-input-placeholder {
    color: white;
  }
  input::placeholder {
    color: white;
  }
  .container-cities {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    overflow: auto;
    height: 100%;
  }
  .no-data {
    display: flex;
    background-color: rgba(0, 0, 0, 0.555);
    color: white;
    border-radius: 15px;
    justify-content: center;
    width: 70%;
    height: 60%;
    align-items: center;
  }
  .container-cities::-webkit-scrollbar {
    width: 5px;
  }
  .container-cities::-webkit-scrollbar-track {
    /* -webkit-box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.3);
    box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.3); */
    background-color: rgba(255, 255, 255, 0);
    /* border-radius: 15px; */
  }

  .container-cities::-webkit-scrollbar-thumb {
    background-color: #ffffffea;
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  }
}

